import { Col, Row, Divider, DatePicker, Checkbox, Modal } from "antd";
import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import DefaultLayout from "../components/DefaultLayout";
import Spinner from "../components/Spinner";
import { getAllCars } from "../redux/actions/carsActions";
import moment from "moment";
import { bookCar } from "../redux/actions/bookingActions";
import StripeCheckout from "react-stripe-checkout";
import AOS from "aos";
import Button from "@mui/joy/Button";
import { Typography } from "@mui/material";
import DrawerAppBar from "../components/Header";
import "aos/dist/aos.css"; // You can also use <link> for styles
const { RangePicker } = DatePicker;
function BookingCar({ match }) {
  const { cars } = useSelector((state) => state.carsReducer);
  const { loading } = useSelector((state) => state.alertsReducer);
  const [car, setcar] = useState({});
  const dispatch = useDispatch();
  const [from, setFrom] = useState();
  const [to, setTo] = useState();
  const [totalHours, setTotalHours] = useState(0);
  const [driver, setdriver] = useState(false);
  const [totalAmount, setTotalAmount] = useState(0);
  const [showModal, setShowModal] = useState(false);

  useEffect(() => {
    if (cars.length == 0) {
      dispatch(getAllCars());
    } else {
      setcar(cars.find((o) => o._id == match.params.carid));
    }
  }, [cars]);

  useEffect(() => {
    setTotalAmount(totalHours * car.rentPerHour);
    if (driver) {
      setTotalAmount(totalAmount + 30 * totalHours);
    }
  }, [driver, totalHours]);

  function selectTimeSlots(values) {
    setFrom(moment(values[0]).format("MMM DD yyyy HH:mm"));
    setTo(moment(values[1]).format("MMM DD yyyy HH:mm"));

    setTotalHours(values[1].diff(values[0], "hours"));
  }

  function onToken(token) {
    const reqObj = {
      token,
      user: JSON.parse(localStorage.getItem("user"))._id,
      car: car._id,
      totalHours,
      totalAmount,
      driverRequired: driver,
      bookedTimeSlots: {
        from,
        to,
      },
    };

    dispatch(bookCar(reqObj));
  }
  const rangePickerStyle = {
    height: "40px",
    borderRadius: "15px",
    border: "none",
    boxShadow: "0px 1px 3px rgba(0, 0, 0, 0.1)",
    padding: "8px",
    fontSize: "14px",
    fontWeight: 600,
    color: "#525252",
    marginBottom: "24px",
    marginTop: "16px",
    transition: "all 0.2s ease-in-out",
    "&:hover, &:focus": {
      outline: "none",
      boxShadow: "0px 1px 6px rgba(0, 0, 0, 0.2)",
    },
  };

  const rangePickerDropdownStyle = {
    boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.1)",
    borderRadius: "20px",
  };
  return (
    <>
      <DrawerAppBar />
      {loading && <Spinner />}
      <Row
        justify="center"
        className="d-flex align-items-center"
        style={{ minHeight: "90vh" }}
      >
        <Col lg={10} sm={24} xs={24} className="p-3">
          <img
            src={car.image}
            className="carimg2 bs1 w-100"
            data-aos="flip-left"
            data-aos-duration="1500"
          />
        </Col>

        <Col lg={10} sm={24} xs={24} className="text-right">
          <Divider type="horizontal" dashed>
            Vehicle Info
          </Divider>

          <div style={{ textAlign: "right" }}>
            <p>{car.name}</p>
            <p>{car.rentPerHour} Rent Per hour /-</p>
            <p>Fuel Type : {car.fuelType}</p>
            <p>Max Persons : {car.capacity}</p>
          </div>
          <Typography sx={{ fontSize: "15px" }}>
            <b>
              ( ***Ensure to have a orginal Aadhaar card and Drving License for
              verification***)
            </b>
          </Typography>
          <Divider type="horizontal" dashed>
            Select Time Slots
          </Divider>
          <RangePicker
            showTime={{ format: "HH:mm" }}
            format="MMM DD yyyy HH:mm"
            onChange={selectTimeSlots}
            style={rangePickerStyle}
            dropdownStyle={rangePickerDropdownStyle}
          />
          <br />
          <Button
            className="btn1c mt-2"
            onClick={() => {
              setShowModal(true);
            }}
          >
            See Booked Slots
          </Button>
          {from && to && (
            <div>
              <p>
                Total Hours : <b>{totalHours}</b>
              </p>
              <p>
                Rent Per Hour : <b>{car.rentPerHour}</b>
              </p>
              <Checkbox
                onChange={(e) => {
                  if (e.target.checked) {
                    setdriver(true);
                  } else {
                    setdriver(false);
                  }
                }}
              >
                Driver Required
              </Checkbox>

              <Checkbox disabled={driver}>Have Driving License</Checkbox>

              <h3>Total Amount : {totalAmount}</h3>

              <StripeCheckout
                shippingAddress
                token={onToken}
                currency="inr"
                amount={totalAmount * 100}
                stripeKey="pk_test_51IYnC0SIR2AbPxU0TMStZwFUoaDZle9yXVygpVIzg36LdpO8aSG8B9j2C0AikiQw2YyCI8n4faFYQI5uG3Nk5EGQ00lCfjXYvZ"
              >
                <Button className="btn1c">Book Now</Button>
              </StripeCheckout>
            </div>
          )}
        </Col>

        {car.name && (
          <Modal
            visible={showModal}
            closable={false}
            footer={false}
            title="Booked time slots"
          >
            <div className="p-2">
              {car.bookedTimeSlots.map((slot) => {
                return (
                  <Button
                    className="btn1c mt-2"
                    sx={{ marginRight: 2 }}
                    cursor="cursor"
                  >
                    {slot.from} - {slot.to}
                  </Button>
                );
              })}

              <div className="text-right mt-5">
                <Button
                  className="btn1c"
                  onClick={() => {
                    setShowModal(false);
                  }}
                >
                  CLOSE
                </Button>
              </div>
            </div>
          </Modal>
        )}
      </Row>
    </>
  );
}

export default BookingCar;
